<template>
  <div class="page-chatroom max-width message-container">
    <div v-if="showScrollBtn" class="scroll-btn">
      <v-icon
          size="40"
          @click="scrollToEnd"
          >mdi-arrow-down-bold-circle
      </v-icon>
    </div>
    <div><v-toolbar flat>
            <v-icon
              large
              class="mr-5"
              color="black"
              @click="$router.push({ name: 'chatList' })"
              >mdi-chevron-left</v-icon
            >
            <v-avatar size="48">
              <v-img
                :src="getImg()"
                class="rounded-circle"
                @click="callNativeApp('uid : test0001'),openInfoDialog()"
              ></v-img>
            </v-avatar>
            <v-toolbar-title class="ml-3" @click="openInfoDialog()">
              {{ storeReceiver.userName }}
              <span class="dot" v-if="storeReceiver.onlineStatus"></span>
              <span v-if="storeReceiver.onlineStatus" style="color: grey; font-size:0.6em;">...</span>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <option-dialog @sentReport="sentReport" ref="optionDialog"></option-dialog>
    </v-toolbar></div>
    <div style="overflow-y: scroll;" id="messageContainer" @click="showEmoji = false">
      <v-card flat class="d-flex flex-column pt-3">
              <v-btn v-if="storeReceiver.message && storeReceiver.message.length==0" text color="#7E7E7E" class="mb-5">
                ~
              </v-btn>
              <infinite-loading
                v-if="storeReceiver.message && storeReceiver.message.length"
                spinner="spiral"
                direction="top"
                @infinite="loadHistoryMsg"
              >
                <div slot="no-more"></div>
                <div slot="no-results"></div>
              </infinite-loading>
              <div v-for="(item, index) in storeReceiver.message" :key="item.id">
                <div
                  class="date mb-5"
                  v-if="
                    index == 0 ||
                      datesAreNotSameDay(
                        storeReceiver.message[index - 1].createdAt,
                        item.createdAt
                      )
                  "
                >
                  {{ dayFormate(item.createdAt) }}
                </div>
                <ul class="px-4">
                  <li
                    class="d-flex align-center mb-5"
                    :class="{ reverse: !isReceiver(item.receiver) }"
                  >
                    <div
                      v-if="item.text !== null"
                      class="text-box-text"
                      :class="isReceiver(item.receiver) ? 'custom' : 'main'"
                    >
                      {{ item.text }}
                    </div>
                    <div v-else>
                      <v-img
                        @click.stop="zoomImg(item.image)"
                        class="text-box-text"
                        :class="isReceiver(item.receiver) ? 'custom' : 'main'"
                        :src="item.image.file"
                        v-bind:alt="item.image.fileName"
                      >
                        <template v-slot:placeholder>
                          <v-row
                            class="fill-height ma-0"
                            align="center"
                            justify="center"
                          >
                            <v-progress-circular
                              indeterminate
                              color="grey lighten-5"
                            ></v-progress-circular>
                          </v-row>
                        </template>
                      </v-img>
                      <v-dialog
                        v-if="selectedImg"
                        v-model="imgDialog"
                        max-width="350px"
                      >
                        <v-card>
                          <v-card-title style="font-size: 0.8em; padding: 5px;">
                            {{ selectedImg.fileName }}
                          </v-card-title>
                          <v-img
                            max-width="350px"
                            :src="selectedImg.file"
                            v-bind:alt="selectedImg.fileName"
                          >
                          </v-img>
                        </v-card> 
                      </v-dialog>
                    </div>
                    <div class="text-box-time">
                      {{ timeFormat(item.createdAt) }}
                    </div>
                  </li>
                </ul>
                <div v-if="getUnreadStatus(item.createdAt)" class="mb-5 unreadBar">
                  <v-icon left small class="unreadBar-icon">
                    mdi-more
                  </v-icon>
                  Unread messages below
                </div>
              </div>
              <div v-if="storeReceiver.isTyping" class="mb-5">
                <div class="typingIndicatorContainer">
                  <div class="typingIndicatorBubble">
                    <div class="typingIndicatorBubbleDot"></div>
                    <div class="typingIndicatorBubbleDot"></div>
                    <div class="typingIndicatorBubbleDot"></div>
                  </div>
                </div>
              </div>
              <div v-if="storeReceiver.isLeft" class="chatroom-leave-message"> {{ storeReceiver.userName }} <br>
                </div>
      </v-card>
    </div>
    <div class="flex-grow-0 tool-bar">
      <div class="tool-emoji py-5 pl-2">
        <v-icon @click="showEmoji = !showEmoji">
          mdi-sticker-emoji
        </v-icon>
      </div>
      <div class="tool-photo py-5">
        <v-file-input
          class="my-0 py-0 px-3"
          @change="sendImages()"
          accept="image/png, image/jpeg, image/bmp"
          prepend-icon="mdi-image"
          v-model="images"
          hide-input
        ></v-file-input>
      </div>
      <div class="tool-text py-5"><input
                  @focus="startTyping"
                  @blur="stopTyping"
                  @keydown="scrollToEnd"
                  @click="scrollToEnd"
                  @keydown.stop.prevent.enter="send"
                  id="input"
                  v-model="msg.content"
                  placeholder="......"
                  type="text"
                  class="textbox"
                  style="width: 100%;
                  background-color: #bdbdbd;
                  height: 32px;
                  border-radius: 16px;
                  padding: 12px;"
                />
      </div>
      <div class="tool-sent py-5 pr-2"><v-icon @click="send" class="pl-3">
                  mdi-send
                </v-icon>
      </div>
      <div v-show="showEmoji" class="tool-emoji-panel">
        <div class="btnLine">
                  <div
                    :class="emojiOn ? 'btn-on' : 'btn-off'"
                    @click="emojiOn = true"
                  >
                    Emoji
                  </div>
                  <div
                    :class="emojiOn ? 'btn-off' : 'btn-on'"
                    @click="emojiOn = false"
                  >
                    GIF
                  </div>
                  
                  <VEmojiPicker v-show="emojiOn" @select="selectEmoji" />
                  <div v-show="!emojiOn" class="div-gif">
                    <v-row>
                      <v-col cols="12" style="padding-bottom: 0px;">
                        <v-text-field
                          append-icon="mdi-magnify"
                          label="Search GIF"
                          solo
                          dense
                          v-model="searchKey"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" v-for="(gif, index) in gifs" :key="index" class="pa-2">
                        <v-img class="gifImg" :src="gif" @click="sendImages(gif)">
                          <template v-slot:placeholder>
                            <v-row
                              class="fill-height ma-0"
                              align="center"
                              justify="center"
                            >
                              <v-progress-circular
                                indeterminate
                                color="grey"
                              ></v-progress-circular>
                            </v-row>
                          </template>
                        </v-img>
                      </v-col>
                    </v-row>
                  </div>
        </div>
      </div>
    </div>
    <user-info-dialog :user="friendDetailData" :dialog.sync="infoDialog" :loading="loadingFriendInfo"></user-info-dialog>
  </div>
</template>
<script>
import imageCompression from 'browser-image-compression';
import { VEmojiPicker } from 'v-emoji-picker';
import InfiniteLoading from 'vue-infinite-loading';
import axios from 'axios';
import { debounce } from "lodash";
import OptionDialog from '../components/ChatroomOptionDialog.vue';
import UserInfoDialog from '../components/UserInfoDialog.vue';
export default {
  components: {
    VEmojiPicker,
    InfiniteLoading,
    OptionDialog,
    UserInfoDialog
  },
  data() {
    return {
      msg: {
        name: 'Angus',
        type: true,
        time: Date.now(),
        content: '',
      },
      images: null,
      imgDialog: false,
      selectedImg: null,
      showEmoji: false,
      emojiOn: true,
      searchKey: '',
      gifs:[],
      gif:'',
      showScrollBtn:false,
      infoDialog:false,
      friendDetailData:{
        pictures:{
          main:{},
          others:{}
        },
        interestlist:[]
      },
      loadingFriendInfo:false,
      prevLastSeenMessageTimeStamp: null,
    }
  },
  computed: {
    storeReceiver(){
      if(this.$route.query.id === "charmbo"){
        return this.$store.state.charmboRoom;
      }
      return this.$store.state.chatRoom.find(
        (e) => e._id === this.$route.query.id
      ) || {message:[],isTyping:false};
    },
  },
  created() {
    this.debounceGifSearch = debounce(this.getGif, 500); //debounce 0.5sec

    //Initialize `prevLastSeenMessageTimeStamp`:  prevent shoiwing unread message notification
    const lastIdx = this.storeReceiver.message.length-1;
    this.prevLastSeenMessageTimeStamp = (lastIdx >= 0)? this.storeReceiver.message[lastIdx].createdAt : null;

    //Detect page close or page change
    window.addEventListener('beforeunload', event => this.saveLastSeenMessageTimestamp(event), {capture: true});
  },
  beforeDestroy() {
    window.removeEventListener('beforeunload', event => this.saveLastSeenMessageTimestamp(event), {capture: true});
  },
  mounted() {
    let self = this;
    this.debounceGifSearch();
    const container = document.getElementById('messageContainer');
    container.onscroll = () => {
      if(container.scrollTop < container.scrollHeight - container.clientHeight){
        self.showScrollBtn = true;
      }
      else
        self.showScrollBtn = false;
    };
    this.$nextTick(() => this.scrollToEnd());
  },
  watch: {
    searchKey() {
      this.debounceGifSearch();
    }
  },
  methods: {
    async send() {
      const vm = this;
      const receiver = this.storeReceiver;
      // avoid sending space
      if (vm.msg.content.length > 0) {
        let sendMessage = {
          chatRoomID: receiver.chatRoomID,
          receiverChatID: receiver._id,
          senderChatID: vm.$store.state.userinfo._id,
          text: vm.msg.content,
          image: {
            file: null,
            fileName: null,
          },
          createdAt: new Date(Date.now()),
        };
        console.log(sendMessage);
        this.$socket.client.emit('send_message', sendMessage);
        let saveData = {
          friendsId: receiver._id,
          data: {
            sender: vm.$store.state.userinfo._id,
            text: vm.msg.content,
            image: {
              file: null,
              fileName: null,
            },
            createdAt: new Date(Date.now()),
          },
        };
        vm.msg.content = '';
        await this.$store.dispatch('actionReceiveMessage', saveData);
        this.scrollToEnd();
      }
    },
    async sendImages(url=undefined) {
      //submit images
      //images : this.images
      console.log('send images');
      const imageFile = this.images;
      const options = {
        maxSizeMB: 0.2,
        maxWidthOrHeight: 600,
        useWebWorker: true,
      };
      const readThenSendFile = (data) => {
        const reader = new FileReader();
        reader.onload = async (event) => {
          const vm = this;
          const receiver = this.storeReceiver;
          await sendfile(event.target.result, data.name, vm, receiver);
        };
        reader.readAsDataURL(data);
        reader.onerror = () => {
          alert('Could not read the file: large file size');
        };
      };
      async function sendfile(file, fileName, vm, receiver) {
        let sendMessage = {
          chatRoomID: receiver.chatRoomID,
          receiverChatID: receiver._id,
          senderChatID: vm.$store.state.userinfo._id,
          text: null,
          image: {
            file: file,
            fileName: fileName,
          },
          createdAt: new Date(Date.now()),
        };
        console.log(sendMessage);
        vm.$socket.client.emit('send_message', sendMessage);
        let saveData = {
          friendsId: receiver._id,
          data: {
            sender: vm.$store.state.userinfo._id,
            text: null,
            image: {
              file: file,
              fileName: fileName,
            },
            createdAt: new Date(Date.now()),
          },
        };
        await vm.$store.dispatch('actionReceiveMessage', saveData);
        vm.scrollToEnd();
      }

      try {
        if (url) {
          const vm = this;
          const receiver = this.storeReceiver;
          const randomName = Math.random().toString(36).substring(7)+'.gif';
          await sendfile(url, randomName, vm, receiver);
        } else {
          const compressedFile = await imageCompression(imageFile, options);
          console.log(
            'compressedFile instanceof Blob',
            compressedFile instanceof Blob
          ); // true
          console.log(
            `compressedFile size ${compressedFile.size / 1024 / 1024} MB`
          ); // smaller than maxSizeMB
          await readThenSendFile(compressedFile);
        } 
      } catch (error) {
        console.log(error);
      }
    },
    isReceiver(id) {
      return this.$store.state.userinfo._id === id;
    },
    callNativeApp(data) {
      try {
        window.webkit.messageHandlers.callbackHandler.postMessage(data);
      } catch (err) {
        console.log('The native context doesnt exist yet!');
      }
    },
    stopTyping() {
      console.log('stopTyping');
      const vm = this;
      const receiver = this.storeReceiver;
      this.$socket.client.emit('typing', {
        receiverChatId: receiver._id,
        senderChatId: vm.$store.state.userinfo._id,
        isTyping: false,
      });
    },
    startTyping() {
      console.log('startTyping');
      const vm = this;
      const receiver = this.storeReceiver;
      this.$socket.client.emit('typing', {
        receiverChatId: receiver._id,
        senderChatId: vm.$store.state.userinfo._id,
        isTyping: true,
      });
    },
    scrollToEnd() {
      const container = document.getElementById('messageContainer');
      if (container) {
        container.scrollTop = container.scrollHeight;
      }
    },
    loadHistoryMsg($state) {
      const receiver = this.storeReceiver;
      const data = {
        friendsId: receiver._id,
        chatRoomID: receiver.chatRoomID,
        lastMessageId: receiver.message[0]._id,
      };
      this.$store
        .dispatch('actionLoadHistoryMessage', data)
        .then((res) => {
          if (res > 0) {
            $state.loaded();
          } else {
            $state.complete();
          }
        })
        .catch(() => {
          $state.error();
        });
    },
    timeFormat(time) {
      let d = new Date(time);
      return (
        ('0' + d.getHours()).slice(-2) + ':' + ('0' + d.getMinutes()).slice(-2)
      );
    },
    datesAreNotSameDay(first, second) {
      let day1 = new Date(first);
      let day2 = new Date(second);
      return !(
        day1.getFullYear() === day2.getFullYear() &&
        day1.getMonth() === day2.getMonth() &&
        day1.getDate() === day2.getDate()
      );
    },
    dayFormate(day) {
      var d = new Date(day);
      var day_list = ['', '', '', '', '', '', ''];
      return (
        d.getFullYear() +
        '/' +
        ('0' + (d.getMonth() + 1)).slice(-2) +
        '/' +
        ('0' + d.getDate()).slice(-2) +
        ' ' +
        day_list[d.getDay()]
      );
    },
    selectEmoji(emoji) {
      console.log(emoji);
      let input = document.getElementById('input');
      let startPos = input.selectionStart;
      let endPos = input.selectionEnd;
      let resultText =
        input.value.substring(0, startPos) +
        emoji.data +
        input.value.substring(endPos);
      input.value = resultText;
      input.focus();
      input.selectionStart = startPos + emoji.data.length;
      input.selectionEnd = startPos + emoji.data.length;
      this.msg.content = resultText;
    },
    getImg() {
      let item = this.storeReceiver;
      if (!item.pictures) {
        return '';
      }
      if(item && item.pictures && item.pictures.main && item.pictures.main.url){
          return item.pictures.main.url;
      }
      let type = item._id || "1234567890abcdef".charAt(Math.floor(Math.random()*16));
      let sex = item.sex;
      if(typeof sex == "undefined" || sex == null)
          sex = "01".charAt(Math.floor(Math.random()*2));
      let s = sex == 0 ? 'g' : 'b';
      let e = type.charAt(type.length - 1);
      item.pictures.main.url = require('../assets/img/avatars/'+s+e+'.svg');
      return item.pictures.main.url;
    },
    getGif() {
      let search_url = '';
      if (this.searchKey) {
        search_url = 'https://api.tenor.com/v1/search?q=' + this.searchKey + '&key=Y760PDCLGW8P' + '&limit=' + 10;
      } else {
        // default trending gifs
        search_url = 'https://api.tenor.com/v1/trending?key=Y760PDCLGW8P' + '&limit=' + 10;
      }
      console.log(search_url);
      axios.get(`${search_url}`).then((response) => {
        console.log(response.data);
        this.gifs = response.data.results.map(result => result.media[0].tinygif.url);
      });
    },
    zoomImg(img) {
      this.imgDialog = true;
      this.selectedImg = img;
    },
    sentReport(param){
      Object.assign(param,{deleteId: this.storeReceiver._id})
      console.log(param);
      //call api
      this.$store.dispatch('actionUnfriend', param).then(() => {
        
      });
      this.$refs.optionDialog.sentOK();
    },
    openInfoDialog(){
      let vm = this;
      this.loadingFriendInfo = true;
      this.infoDialog = true;
      this.$store.dispatch('actionFriendinfo', this.storeReceiver._id).then((response) => {
        vm.friendDetailData = response;
        vm.loadingFriendInfo = false;
      });
    },
    getUnreadStatus(messageTimeStamp) {
      if (messageTimeStamp) {
        const lastIdx = this.storeReceiver.message.length - 1;
        if (
          lastIdx >= 0 &&
          this.storeReceiver.message[lastIdx].createdAt !== messageTimeStamp && //not last message
          this.storeReceiver.lastSeenMessageTimeStamp === messageTimeStamp && // match lastSeenMessageTimeStamp in vuex and DB
          this.prevLastSeenMessageTimeStamp > messageTimeStamp // the timeStamp should be smaller than the last one when first enter chatRoom
        ) {
          return true;
        }
      }
      return false;
    },
    saveLastSeenMessageTimestamp() {
      console.log('onbeforeUnload')
      if (this.storeReceiver.message) {
        const lastIdx = this.storeReceiver.message.length - 1;
        if (lastIdx >= 0) {
          // this.$store.dispatch('actionSaveLastSeenMessageTimeStamp', {
          //   lastSeenMessageTimeStamp: this.storeReceiver.message[lastIdx].createdAt,
          //   id: this.storeReceiver._id,
          // });
        }
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      !vm.$route.query.id && vm.$router.push({ name: 'chatList' });
      if (from.path !== '/chatlist') {
        vm.$store.dispatch('actionFriendList');
      }
    })
  },
  async beforeRouteLeave (to, from, next) {
    if (this.storeReceiver.message) {
      const lastIdx = this.storeReceiver.message.length - 1;
      if (
        lastIdx >= 0 &&
        this.storeReceiver.lastSeenMessageTimeStamp !== this.storeReceiver.message[lastIdx].createdAt
      ) {
        // await this.$store.dispatch('actionSaveLastSeenMessageTimeStamp', {
        //   lastSeenMessageTimeStamp: this.storeReceiver.message[lastIdx].createdAt,
        //   id: this.storeReceiver._id,
        // })
      }
    }
    next();
  },
};
</script>
<style scoped>
.v-dialog__content {
  align-items: center;
  justify-content: flex-start;
}
.v-overlay__scrim {
  width: 400px;
}
.text-box-text {
  padding: 8px 12px;
  max-width: 264px;
  font-size: 16px;
}
.text-box-text.custom {
  background-color: #d5d5d5;
  border-radius: 4px 24px 24px 24px;
  margin-right: 12px;
}
.text-box-text.main {
  background-color: #8d8d8d;
  border-radius: 24px 24px 4px 24px;
  margin-left: 12px;
}
li:hover .text-box-time {
  display: inline-block;
}
.text-box-time {
  color: #979797;
  font-size: 12px;
  display: none;
}
.reverse {
  flex-direction: row-reverse;
}
.h-100 {
  height: 100%;
}
.typingIndicatorContainer {
  display: flex;
  flex: none;
  align-items: flex-end;
  margin: $spacing4 0;
}

.typingIndicatorBubble {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 40px;
  margin: 0px 8px;
  background-color: #d5d5d5;
  border-radius: 4px 24px 24px 24px;
}

.typingIndicatorBubbleDot {
  width: 4px;
  height: 4px;
  margin-right: 4px;
  background-color: #57585a;
  border-radius: 50%;
  animation-name: bounce;
  animation-duration: 1.3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.typingIndicatorBubbleDot:first-of-type {
  margin: 0px 4px;
}

.typingIndicatorBubbleDot:nth-of-type(2) {
  animation-delay: 0.15s;
}

.typingIndicatorBubbleDot:nth-of-type(3) {
  animation-delay: 0.3s;
}
.date {
  color: #747070;
  font-size: 11px;
  line-height: 13px;
  text-align: center;
}
.btnLine {
  text-align: center;
}
.btn-on {
  display: inline-block;
  width: 144px;
  height: 26px;
  line-height: 26px;
  background: #7b7b7b;
  border-radius: 32px;
  color: #ffffff;
  margin: 10px;
  cursor: pointer;
}
.btn-off {
  display: inline-block;
  width: 144px;
  height: 26px;
  line-height: 26px;
  background: #c4c4c4;
  border-radius: 32px;
  color: #ffffff;
  margin: 10px;
  cursor: pointer;
}
.div-gif {
  height: 433px;
  padding: 0 12px;
  overflow: auto;
}
.gifImg:hover {
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
  opacity: .5;
}
.page-chatroom{
    display: grid;
    height: 100vh;
    grid-template-rows: auto 1fr auto;
    gap: 16px;
}
.tool-bar{
  display: grid;
  grid-template-rows: 72px auto;
  grid-template-columns: auto auto 1fr auto;
  background-color:#E0E0E0;
}
.tool-emoji{
  vertical-align: middle;
  line-height: 32px;
}
.tool-sent{
  vertical-align: middle;
  line-height: 32px;
}
.tool-emoji-panel{
  grid-column: 1 / span 4;
}
.chatroom-leave-message{
  font-size: 12px;
  color: #7E7E7E;
  text-align: center;
}
.message-container{
  position: relative;
}
.unreadBar {
  text-align: center;
  font-size: 0.8rem; 
  color: black; 
  background-color: rgba(128, 128, 128, 0.1);
}
.unreadBar .unreadBar-icon {
  margin: auto 5px;
  float: left;
  display: flex;
  align-items: center;
  color: grey;
}
.scroll-btn{
  position:absolute;
  z-index: 1;
  top: calc(100vh - 150px);
  left: calc(100%/2 - 18px);
}
@keyframes bounce {
  0%,
  60%,
  100% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(-4px);
  }
}
</style>
